import React, { useState, useEffect } from 'react';
import articles from '../article-content';
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../components/ComponentsList';
import UpvoteSection from "../components/UpvoteSection";
import AddCommentForm from "../components/AddCommentForm"

const ArticlePage = ({match}) => {
   const [articleInfo, setArticleInfo] = useState({upvotes:0, comments:[]});
   const name = match.params.name;
    const article = articles.find(article => article.name === name);

   useEffect(() => {

      const fetchData = async () => {
         const result = await fetch(`/api/articles/${name}`);
         const body = await result.json();
         setArticleInfo(body);
         console.log(body)
      }
      fetchData();
   },  [name])

    if (!article) return <NotFoundPage />

    const relatedArticles = articles.filter(item => item.name !== name);
   
   return (
   <>
    <h1>{article.title}</h1>
    <UpvoteSection upvotes={articleInfo.upvotes} articleName={name} setArticleInfo={setArticleInfo} />
    {article.content.map(paragraph => <p>{paragraph}</p>)}
    <CommentsList comments={articleInfo.comments} />
    <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
    <h2>Related Articles</h2>
    <ArticlesList articles={relatedArticles} />
    </>
   );
};

export default ArticlePage;